			<button class="btn-primary" @click="rotateSx()">{{ $t('contact.write') }}</button>
		</div>
	</div>
	<div class="back side">
		<div class="content">
			<h1>{{ $t('contact.write') }}</h1>
			<form method="POST" id="form" active="../php/_sendmail.php">
				<label>{{ $t('contact.name') }} :</label>
				<input type="text" name="name" required>
