<script>
import Popup from "../components/Popup.vue"
import { gsap } from "gsap";
import { ref } from 'vue'
export default {
	components:{
		'app-popup':Popup
	},
	setup(){
		var popup = ref()
		const rotateSx = () =>{
			gsap.to($('.container'), {
				rotationY: "180deg",
				ease:'power2.inOut'
			});
		}
		const rotateDx = () =>{
			gsap.to($('.container'), {
				rotationY: "0deg",
				ease:'power2.inOut'
			});
			document.getElementById("form").reset()
		}
		const sendEmail = () => {
			
			//send email animation
			document.querySelectorAll('.send').forEach(button => {
			let getVar = variable => getComputedStyle(button).getPropertyValue(variable);
			button.addEventListener('click', e => {

				if(!button.classList.contains('active')) {

					button.classList.add('active');

					gsap.to(button, {
						keyframes: [{
							'--left-wing-first-x': 50,
							'--left-wing-first-y': 100,
							'--right-wing-second-x': 50,
							'--right-wing-second-y': 100,
							duration: .2,
							onComplete() {
								gsap.set(button, {
									backgroundColor: 'transparent',
									'--left-wing-first-y': 0,
									'--left-wing-second-x': 40,
									'--left-wing-second-y': 100,
									'--left-wing-third-x': 0,
									'--left-wing-third-y': 100,
									'--left-body-third-x': 40,
									'--right-wing-first-x': 50,
									'--right-wing-first-y': 0,
									'--right-wing-second-x': 60,
									'--right-wing-second-y': 100,
									'--right-wing-third-x': 100,
									'--right-wing-third-y': 100,
									'--right-body-third-x': 60
								})
							}
						}, {
							'--left-wing-third-x': 20,
							'--left-wing-third-y': 90,
							'--left-wing-second-y': 90,
							'--left-body-third-y': 90,
							'--right-wing-third-x': 80,
							'--right-wing-third-y': 90,
							'--right-body-third-y': 90,
							'--right-wing-second-y': 90,
							duration: .2
						}, {
							'--rotate': 50,
							'--left-wing-third-y': 95,
							'--left-wing-third-x': 27,
							'--right-body-third-x': 45,
							'--right-wing-second-x': 45,
							'--right-wing-third-x': 60,
							'--right-wing-third-y': 83,
							duration: .25
						}, {
							'--rotate': 55,
							'--plane-x': -8,
							'--plane-y': 24,
							duration: .2
						}, {
							'--rotate': 40,
							'--plane-x': 45,
							'--plane-y': -180,
							'--plane-opacity': 0,
							duration: .3,
							onComplete() {
								gsap.to(button, {
									cursor: 'default'
								})
							}
						}]
					})

					gsap.to(button, {
						keyframes: [{
							'--text-opacity': 0,
							'--border-radius': 0,
							'--left-wing-background': getVar('--primary-darkest'),
							'--right-wing-background': getVar('--primary-darkest'),
							duration: .1
						}, {
							'--left-wing-background': getVar('--primary'),
							'--right-wing-background': getVar('--primary'),
							duration: .1
						}, {
							'--left-body-background': getVar('--primary-dark'),
							'--right-body-background': getVar('--primary-darkest'),
							duration: .4
						}, {
							'--success-opacity': 1,
							'--success-scale': 1.5,
							duration: .25,
							delay: .25
						}]
					})

				}

			})

			document.getElementById("form").reset()
		});
		}
		function openPopUp(){ popup.value.openMap()}

		function call(){
			window.open("callto://393472458581");
		}

		function write(){
			window.open("mailto:franzdesign@email.it");
		}

		var footer = document.querySelector("footer"); 
		footer.style.display = "block";
		if(window.matchMedia("(max-width: 720px)").matches)
			footer.style.marginTop = "400px";
		else
			footer.style.marginTop = "50px";
		footer.style.paddingBottom = "50px";
		footer.style.backgroundColor = "none";

		return {rotateSx,rotateDx,sendEmail,openPopUp,call,write,popup}
	}
}
</script>

