<script>
// Imports
import GSAP_Gallery from "../components/GSAP_Gallery.vue";
import { ref, onMounted } from 'vue';
import { gsap, ScrollTrigger } from "gsap/all";

export default {
	components: {
      'app-gallery': GSAP_Gallery
    },
    setup() {
      const galleryRef = ref();
      onMounted(() => {
        gsap.registerPlugin(ScrollTrigger);
        galleryRef.value.load();
        animation();
        slideTitle();
      });
      const openLink = (social) => {
        if(social=="fb")
          window.open("https://www.facebook.com/franzdesign-125927074115534/");
        else
          window.open("https://www.instagram.com/piterfranzdesign/");
      }
      
      const animation = () => {
        var tl = new gsap.timeline();
      
        tl.set('.logo',{y:'-150%'})
        tl.set('.secondary',{opacity:'0'})
        tl.set('.facebook, .instagram, .infoGallery h1',{x:'-850px'})
        tl.set('.infoGallery text',{x:'-600px', display:'none'})
        tl.set('.infoGallery button',{x:'-600px'})

        tl.to('.logo',2,{y:'-50%'})
        tl.to('.facebook',1,{x:'-90%'})
        tl.to('.instagram',1.2,{x:'-100%'})
        tl.to('.secondary',10,{opacity:'1'})
        tl.to('.infoGallery h1', {
        autoAlpha: 1,
        xPercent: '300',
        ease: "none",
        scrollTrigger: {
          trigger: ".gallery",
          start: "bottom bottom",
          end: "top center",
          scrub: 1
        }
      })
      tl.to('.infoGallery text', {
        autoAlpha: 1,
        display: "block",
        xPercent: '205',
        ease: "none",
        scrollTrigger: {
          trigger: ".gallery",
          start: "bottom bottom",
          end: "center center",
          scrub: 1
        }
      })
      tl.to('.infoGallery button', {
        autoAlpha: 1,
        xPercent: '550',
        ease: "none",
        scrollTrigger: {
          trigger: ".gallery",
          start: "bottom bottom",
          end: "center center",
          scrub: 1
        }
      })
      }

        const slideTitle = () => {

          gsap.to("#text",{
                xPercent:150,
                ease: "none",
                scrollTrigger: {
                  trigger: ".contact",
                  start: "top bottom",
                  center: "center top",
                  scrub: 1
                }
          })
          gsap.to("#mail",{
                transformOrigin:"center",
                opacity: 0.5,
                rotation:100,
                scale:800/600,
                xPercent:300,
                ease: "none",
                scrollTrigger: {
                  trigger: ".contact",
                  start: "top bottom",
                  end: "bottom top",
                  scrub: 1
                }
          })
          gsap.to("#tel",{
                transformOrigin:"center",
                opacity: 0.7,
                rotation:180,
                scale:1.3,
                xPercent:100,
                ease: "none",
                scrollTrigger: {
                  trigger: ".contact",
                  start: "top bottom",
                  end: "bottom top",
                  scrub: 1
                }
          })
          gsap.to("#map",{
                transformOrigin:"center",
                opacity: 0.3,
                rotation:-100,
                scale:3,
                xPercent:50,
                ease: "none",
                scrollTrigger: {
                  trigger: ".contact",
                  start: "top bottom",
                  end: "bottom top",
                  scrub: 1
                }
          })
        }
        
      var footer = document.querySelector("footer")
      footer.style.display = "block";
      footer.style.paddingBottom = "50px"
      if(window.matchMedia("(max-width: 720px)").matches)
        footer.style.marginTop = "-20%"
      else
        footer.style.marginTop = "-10%"
      footer.style.backgroundImage = "linear-gradient(#fff0 0%, #141928 45%)"

      return {galleryRef,openLink,slideTitle}
    }
}
</script>

