<script>
import { gsap, ScrollTrigger } from "gsap/all";
import { onMounted } from 'vue';

export default {
	setup() {
    gsap.registerPlugin(ScrollTrigger);
    onMounted(() => {
        scrollAnimation();
      });
    
    const scrollAnimation = () => {
      
      var tl = new gsap.timeline();
      
      tl.set('h1, .right p, .row2 p',{x:'+150%'})
      tl.set('.row1 p',{x:'-150%'})
      tl.set('.image.dx',{x:'150%'})
      tl.set('.image.sx',{x:'-150%'})
      tl.set('#signature',{display:'none'})

      tl.to('h1',1,{x:'0%'})
      tl.to('.right p',0.5,{x:'0%'}, "+=0.3")

      tl.to('.row1 p', 2, {
        autoAlpha: 1,
        x: '0',
        ease: "none",
        scrollTrigger: {
          trigger: ".row1",
          start: "bottom bottom",
          scrub: 1
        }
      }).to('.image.dx', 2, {
        xPercent: '-150',
        ease: "power1.in",
        scrollTrigger: {
          trigger: ".row1",
          start: "top center",
          end: "center center",
          scrub: 1
        }
      })
      tl.to('.row2 p', 2, {
        autoAlpha: 1,
        x: '0',
        ease: "none",
        scrollTrigger: {
          trigger: ".row2",
          start: "bottom bottom",
          scrub: 1
        },
      }).to('.image.sx', 2, {
        xPercent: '150',
        ease: "power1.in",
        scrollTrigger: {
          trigger: ".row2",
          start: "center bottom",
          end: "bottom bottom",
          scrub: 1
        }
      })
      tl.to('#signature', 2, {
        display: "inline",
        ease: "none",
        scrollTrigger: {
          trigger: ".row2",
          start: "center center",
          scrub: 1
        },
      })
    }

	  var footer = document.querySelector("footer");
    footer.style.display = "none";
    footer.style.paddingBottom = "0px";
	  footer.style.backgroundImage = "none";
	  
    }
}
</script>

